export default {
	methods:{
		getUserInfo(){
			wx.getUserProfile({
				desc: '用于完善会员资料', // 声明获取用户个人信息后的用途，后续会展示在弹窗中，请谨慎填写
				success: (res) => {
					console.log(res)
					// 保存用户信息到storage
					wx.setStorageSync('userInfo_key',res.userInfo)
					// 返回到个人中心页面
					wx.reLaunch({
						url:'/pages/center/center'
					})
				}
			})
		}
	}
};
